<template> 
    <div>
        <div>
        <ul>
            <li v-for="(product, i) in cartProducts" :key="i"> 
              ...
              <label>
                <input type="number"/>
                <button
                  @click="updateQuantity({product, quantity: $event})"
                >
                  Add more
                </button>
              </label> 
              <button        
                @click="removeFromCart({product})"
              >
                Remove from cart
              </button>                   
            </li>
        </ul>
        <span>
            {{cartTotals.total}}
        </span>
        <span>
            {{cartTotalItems}}
        </span>
        <button
            @click="clearCart(cart.value)"
        >
            Clear cart
        </button>
        </div>
        <div>
          <ul>
            ...
            <li v-for="(product, i) in wishlistProducts" :key="i">
              <button
                  @click="removeFromWishlist(product)"
              >
                  Remove from wishlist
              </button>
            </li>
          </ul>
          <span>
              {{wishlistTotals.total}}
          </span>
          <span>
              {{wishlistTotalItems}}
          </span>
          <button
              @click="clearWishlist"
          >
              Clear wishlist
          </button>
        </div>
    </div>
</template>   
<script>      
  import { computed } from '@vue/composition-api';
  import { useCart, cartGetters, useWishlist, wishlistGetters } from '@vue-storefront/boilerplate';
  import { onSSR } from '@vue-storefront/core';
  export default {
    setup() {
      const {
        wishlist,
        removeItem: removeFromWishlist,
        clear: clearWishlist,
        load: loadWishlist
      } = useWishlist();
      const {
        cart,
        clear: clearCart,
        removeItem: removeFromCart, 
        updateItemQty,
        loading,
      } = useCart();

      const cartProducts = computed(() => cartGetters.getItems(cart.value));
      const cartTotals = computed(() => cartGetters.getTotals(cart.value));
      const cartTotalItems = computed(() => cartGetters.getTotalItems(cart.value));

      const wishlistProducts = computed(() => wishlistGetters.getItems(wishlist.value));
      const wishlistTotals = computed(() => wishlistGetters.getTotals(wishlist.value));
      const wishlistTotalItems = computed(() => wishlistGetters.getTotalItems(wishlist.value));

      onSSR(async () => {
        await loadWishlist();
      });

      return {
        cartProducts,
        cartTotals,
        cartTotalItems,
        removeFromCart,
        cart,
        updateItemQty,
        clearCart, 
        wishlistProducts,
        wishlistTotals,
        wishlistTotalItems,
        wishlist,                
        removeFromWishlist, 
        clearWishlist
      };
    }
  };
</script>